$color2: #2E4F4F;

.newsletters {
  width: -webkit-fill-available;
  min-height: 75vh;
  padding: 50px;
  display: grid;
  grid-auto-flow: column;
  grid-column-gap: 40px;
  grid-template-columns: 220px auto;
  .menu {
    border-right: 2px solid #FFFFFF;
    color: #FFFFFF;
  }
}

.menu-active {
  color: $color2;
}

.buttons-container {
  display: grid;
  align-items: center;
  justify-self: self-end;
  grid-auto-flow: column;
  grid-column-gap: 10px;
}

.info-newslatter {
  width: -webkit-fill-available;
  min-height: 85vh;
  padding: 50px;
}

.info-newslatter-buttons {
  display: grid;
  grid-template-columns: auto;
  width: fit-content;
  grid-auto-flow: column;
  grid-column-gap: 15px;
}

.file-name {
  display: grid;
  grid-template-columns: auto;
  width: fit-content;
  align-items: center;
  grid-column-gap: 5px;
  grid-auto-flow: column;
  & label {
    max-width: 100px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}